import "./TimeList.style.scss";
import React from "react";

const TimeList = ({ data, titlu, c1, c2, c3 }) => {
  return (
    <div className="TimeList">
      <div className="st">
        <p className="data">{data}</p>
        <div className="vl"></div>
      </div>
      <div className="dr">
        <h2>{titlu}</h2>
        <h3>{c1}</h3>
        <h3>{c2}</h3>
        <h3>{c3}</h3>
      </div>
    </div>
  );
};

export default TimeList;
