import "./DotList.style.scss";
import React from "react";

const DotList = ({ data, titlu, c1, c2, c3 }) => {
  return (
    <div className="DotList">
      <div className="st">
        <span className="dot"></span>
        <div className="vl"></div>
      </div>
      <div className="dr">
        <h2>{titlu}</h2>
        <h3>{c1}</h3>
        <h3>{c2}</h3>
      </div>
    </div>
  );
};

export default DotList;
