import { useState, useRef, useEffect } from "react";

import { SocialIcon } from "react-social-icons";
import imgProfil from "./assets/profil.jpg";

import "./cards.scss";

import SkillBar from "./components/SkillBar/SkillBar.component.jsx";
import TimeList from "./components/TimeList/TimeList.component";
import DotList from "./components/DothList/DothList.component";
import BurgerMenu from "./components/BurgerMenu/BurgerMenu.component";

function App() {
  const [isBurgerOpen, setBurger] = useState(false);
  const refCards = useRef();

  // console.log("reload");
  const setBurgerState = () => {
    const a = !isBurgerOpen;
    setBurger(a);
    // console.log(isBurgerOpen);
  };

  const handleDownload = () => {
    const pdfUrl = "./CV-RaulAdrianGherman.pdf";

    const link = document.createElement("a");
    link.href = pdfUrl;
    link.download = "CV-RaulAdrianGherman.pdf";
    link.click();
  };

  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        // console.log(entry)
        
        if (entry.isIntersecting ) {
          // entry.target.classList.add("animateCard");
          // console.log(entry.target.children);
          
          var children = entry.target.querySelectorAll(":scope > *")
          console.log("vizibil");
          if(children){
            console.log("child", children);
            children.forEach(child => {
              child.classList.add('animateCard');
            });
          }
          entry.target.classList.add("selected");
          
        } 
        else if(entry.target.classList.contains("selected")){
          // entry.target.classList.remove("animateCard");
          
          var children = entry.target.querySelectorAll(":scope > *")
          console.log("invizibil");
          if(children){
              console.log("child", children);
              children.forEach(child => {
                child.classList.remove('animateCard');
              });
          }
          entry.target.classList.remove("selected");
        }
      });
    },{ rootMargin: "0px",threshold: 0.2,});
    const animOnScrollElement = document.querySelectorAll(".card");
    animOnScrollElement.forEach((el) => observer.observe(el));
  }, []);

  return (
    <div className="App">
      <div className="phone navbar">
        <h1 className="flothing-name">Raul Gherman</h1>
        <div className="hamburger-button" onClick={setBurgerState}>
          <div className={"bar1 " + (isBurgerOpen ? "change" : null)}></div>
          <div className={"bar2 " + (isBurgerOpen ? "change" : null)}></div>
          <div className={"bar3 " + (isBurgerOpen ? "change" : null)}></div>
        </div>
        {/* {isBurgerOpen && <BurgerMenu/>} */}
        <BurgerMenu show={isBurgerOpen} />
      </div>
      <div className="details_container">
        <div className="informations">
          <img src={imgProfil} className="img_profil_details" alt="" />
          <h1 className="nume">Raul Gherman</h1>
          <h2 className="job">Student</h2>
          <div className="socials">
            <SocialIcon
              url="https://www.facebook.com/raul.gherman.77/"
              bgColor="none"
              fgColor="white"
            />
            <SocialIcon
              url="https://github.com/RaulGhr"
              bgColor="none"
              fgColor="white"
            />
            <SocialIcon
              url="https://www.linkedin.com/in/raul-gherman-4b6aa21bb/"
              bgColor="none"
              fgColor="white"
            />
          </div>
          <a className="cv_download" onClick={handleDownload}>
            Download CV
          </a>
        </div>
        <div className="cards" ref={refCards}>
          <div className="card card-prezentare">
            <div className="continut">
              <img src={imgProfil} className="img_profil_card" alt="" />
              <div className="text">
                <h1>Raul Gherman</h1>
                <h2 className="phone">Student</h2>
                <p className="prezentare">
                Passionate and ambitious Computer Science student. 
                With a deep-rooted love for technology and a knack for problem-solving, 
                I am constantly seeking opportunities to expand my skills. 
                Through my coursework and personal projects, 
                I have gained expertise in Web Full-Stack Technologies and Object Oriented Programming.
                </p>
              </div>
            </div>
            <i className="fas fa-angle-down sageata_next"></i>
          </div>

          <div className="card card-coding">
            <h1 className="titlu-card">Coding Skills</h1>
            <div className="continut">
              <div className="grafice">
                <SkillBar nume={"HTML/CSS"} procent={60} />
                <SkillBar nume={"Java"} procent={40} />
                <SkillBar nume={"React"} procent={40} />
                <SkillBar nume={"C++"} procent={45} />
                <SkillBar nume={"JavaScript"} procent={40} />
                <SkillBar nume={"Python"} procent={50} />
              </div>
            </div>
            <i className="fas fa-angle-down sageata_next"></i>
          </div>

          <div className="card card-education">
            <h1 className="titlu-card">Education</h1>
            <div className="continut">
              <TimeList
                data={"2022-Present"}
                titlu={"University"}
                c1={"Babes-Bolyai University of Cluj-Napoca"}
                c2={"Mathematics and Computer Science"}
                c3={"Bachelor in Computer Science"}
              />
              <TimeList
                data={"2018-2022"}
                titlu={"High School"}
                c1={'Colegiul National "Horea, Closca si Crisan"'}
                c2={"Mathematics and Computer Science"}
              />
            </div>
            <i className="fas fa-angle-down sageata_next"></i>
          </div>

          <div className="card card-experiences">
            <h1 className="titlu-card">Experiences</h1>
            <div className="continut">
              
              <DotList
                titlu={"Dress Up"}
                c1={
                  "A full-stack wardrobe organizer! Capture clothing items, and the API removes backgrounds with a AI Model, compresses, and resizes images before securely storing them in the DB. The user can easily create stylish outfits, and share them with friends for feedback."
                }
                c2={"Technologies used: HTML, CSS, React, Python, MySQL, ML"}
              />
              <DotList
                titlu={"Boundless Date"}
                c1={
                  "Website made in collaboration with Interact club that helps high school student from Alba Iulia to find a date for Valentine's Day by making an account and complete a personality test. 800 accounts were created in 3 days."
                }
                c2={"Technologies used: HTML, CSS, JavaScript, PHP, MySQL"}
              />
              <DotList
                titlu={"Stock Forecasting AI Model"}
                c1={
                  "AI stock price forecasting project on Corsair stock, which is trained on daily stock prices, computing indices, and extracted crucial data from quarterly reports. All information extracted from the pipe is stored in an Influx DB hosted on Google Cloud."
                }
                c2={
                  "Technologies used: Python, Google Cloud, Influx DB"
                }
              />
              <DotList
                titlu={"Task automation platform"}
                c1={
                  "Website that based on a subscription, helps users automate certain tasks by giving them access to a script."
                }
                c2={
                  "Technologies used: HTML, CSS, JavaScript, PHP, MySQL, Python, AWS"
                }
              />
            </div>
            <i className="fas fa-angle-down sageata_next"></i>
          </div>

          <div className="card card-social">
            <h1 className="titlu-card">Volunteer</h1>
            <div className="continut">
            <DotList
                titlu={
                  "Participation in First Tech Challange with Robotics Team XEO"
                }
                c1={
                  "Competition that const in designing, building, and programming robots which need to complete certain challenges but also in interaction between teams and organizing events."
                }
              />
              <DotList
                titlu={"Member of Interact Alba Iulia"}
                c1={
                  "A nongovernmental organization for young people aged 1418 that organize projects that helps their school or community."
                }
              />
              <DotList
                titlu={"Volunteer at an adventure park"}
                c1={
                  "My role was to provide assistance, support, and ensure the safety and enjoyment of visitors."
                }
              />
            </div>
            {/* <i className="fas fa-angle-down sageata_next"></i> */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
